import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { AsyncTypeahead } from 'react-bootstrap-typeahead';
import { Option } from 'react-bootstrap-typeahead/types/types';
import { MessageApiSuccessResponse } from '../../types/MessageApiResponse';
import { MessageApiEventType } from '../../types/MessageApiEventType';
import MessageApi from '../../services/messageApi';
import { User } from '../../types/User';
import { useWebSocket } from '../hooks/useWebSocket';

interface NewMessageProps {
    name: string;
}

const NewMessage = ({ name }: NewMessageProps) => {
    const [isLoading, setIsLoading] = useState(false);
    const [validated, setValidated] = useState(false);
    const [options, setOptions] = useState<string[]>([]);
    const [receiverName, setReceiverName] = useState('');
    const [title, setTitle] = useState('');
    const [body, setBody] = useState('');

    const handleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        e.stopPropagation();
        if (e.currentTarget.checkValidity()) {
            const obj = {
                type: MessageApiEventType.NewMessage,
                payload: {
                    senderName: name,
                    receiverName: receiverName,
                    title,
                    body,
                },
            };
            MessageApi.getInstance().sendMessage(JSON.stringify(obj));
        }
        setValidated(true);
    };

    const handleAutoCompletion = (response: MessageApiSuccessResponse<unknown>) => {
        if (response.type === MessageApiEventType.GetUsersLikeName) {
            const users = response.data as User[];
            setOptions(users.map((it) => it.name));
            setIsLoading(false);
        }
    };

    useWebSocket(handleAutoCompletion);

    const handleSearch = (query: string) => {
        setIsLoading(true);
        const obj = {
            type: MessageApiEventType.GetUsersLikeName,
            payload: {
                name: query,
            },
        };
        setReceiverName(query);
        MessageApi.getInstance().sendMessage(JSON.stringify(obj));
    };

    return (
        <Form noValidate validated={validated} onSubmit={handleFormSubmit}>
            <Form.Group className="mb-3" controlId="recipient">
                <Form.Label>Recipient</Form.Label>
                <AsyncTypeahead
                    inputProps={{ required: true }}
                    id="recipient"
                    isLoading={isLoading}
                    minLength={1}
                    onSearch={handleSearch}
                    onChange={(options: Option[]) => {
                        setReceiverName(options[0].toString());
                    }}
                    options={options}
                    placeholder="Search a user..."
                    isInvalid={receiverName.length === 0 && validated}
                />
                <Form.Control.Feedback
                    className={receiverName.length === 0 && validated ? 'd-block' : ''}
                    type="invalid"
                >
                    At least one character
                </Form.Control.Feedback>
            </Form.Group>
            <Form.Group className="mb-3" controlId="title">
                <Form.Label>Title</Form.Label>
                <Form.Control
                    name="title"
                    type="text"
                    placeholder="Enter a subject"
                    value={title}
                    onChange={(evt) => setTitle(evt.target.value)}
                    required
                />
                <Form.Control.Feedback type="invalid">At least one character</Form.Control.Feedback>
            </Form.Group>
            <Form.Group className="mb-3" controlId="body">
                <Form.Label>Enter a message</Form.Label>
                <Form.Control
                    name="body"
                    as="textarea"
                    rows={3}
                    value={body}
                    onChange={(evt) => setBody(evt.target.value)}
                    required
                />
                <Form.Control.Feedback type="invalid">At least one character</Form.Control.Feedback>
            </Form.Group>
            <Button variant="primary" type="submit">
                Submit
            </Button>
        </Form>
    );
};

export default NewMessage;
