import { useEffect } from 'react';
import MessageApi from '../../services/messageApi';
import { MessageApiSuccessResponse } from '../../types/MessageApiResponse';

export type WebSocketCallback = (response: MessageApiSuccessResponse<unknown>) => void;

export const useWebSocket = (callback: WebSocketCallback) => {
    useEffect(() => {
        MessageApi.getInstance().subscribeForMessages(callback);

        return () => {
            MessageApi.getInstance().unSubscribeForMessages(callback);
        };
    }, []);
};
