import React, { useEffect, useState } from 'react';
import MessageApi from '../../services/messageApi';
import { MessageApiSuccessResponse } from '../../types/MessageApiResponse';
import { MessageApiEventType } from '../../types/MessageApiEventType';
import { DetailedMessage } from '../../types/DetailedMessage';
import InboxItem from '../inboxItem/inboxItem';
import { Accordion } from 'react-bootstrap';
import { useWebSocket } from '../hooks/useWebSocket';

interface InboxProps {
    userId: number;
}

const Inbox = ({ userId }: InboxProps) => {
    const [messages, setMessages] = useState<DetailedMessage[]>([]);

    const handleNewMessages = (response: MessageApiSuccessResponse<unknown>) => {
        if (response.type === MessageApiEventType.GetAllMessages) {
            const messages = response.data as DetailedMessage[];
            setMessages(messages);
        }
        if (response.type === MessageApiEventType.NewMessage) {
            const newMessage = response.data as DetailedMessage;
            setMessages((prevState) => [newMessage, ...prevState]);
        }
    };

    useWebSocket(handleNewMessages);

    useEffect(() => {
        const obj = {
            type: 'messageApi/getAllMessages',
            payload: {
                userId,
            },
        };
        MessageApi.getInstance().sendMessage(JSON.stringify(obj));
    }, []);

    return (
        <div>
            <h2>Messages:</h2>
            <Accordion>
                {messages.map((msg) => (
                    <InboxItem key={msg.id} message={msg} />
                ))}
            </Accordion>
        </div>
    );
};

export default Inbox;
