import { MessageApiSuccessResponse } from '../types/MessageApiResponse';

export type MessageReceivedFn = (response: MessageApiSuccessResponse<unknown>) => void;

export default class MessageApi {
    private static instance = new MessageApi();

    private ws: WebSocket;
    private subscribers: MessageReceivedFn[];

    private constructor() {
        this.ws = new WebSocket(`wss://${window.location.host}`);
        this.subscribers = [];
        this.ws.onmessage = (event: MessageEvent<string>) => {
            const data = JSON.parse(event.data);
            this.subscribers.forEach((it) => it(data));
        };
    }

    public subscribeForMessages(subscriber: MessageReceivedFn) {
        this.subscribers.push(subscriber);
    }

    public unSubscribeForMessages(subscriber: MessageReceivedFn) {
        this.subscribers = this.subscribers.filter((it) => it !== subscriber);
    }

    public sendMessage(payload: string) {
        this.ws.send(payload);
    }

    public get Ready() {
        return this.ws.readyState === this.ws.OPEN;
    }

    public whenReady(callback: () => void) {
        this.ws.onopen = () => {
            callback();
        };
    }

    static getInstance() {
        return MessageApi.instance;
    }
}
