import React from 'react';
import { Accordion, Col, Container, Row } from 'react-bootstrap';
import { DetailedMessage } from '../../types/DetailedMessage';
import dayjs from 'dayjs';

interface InboxItemProps {
    message: DetailedMessage;
}

const InboxItem = ({ message }: InboxItemProps) => {
    return (
        <Accordion.Item eventKey={message.id.toString()}>
            <Accordion.Header>
                <Container fluid>
                    <Row>
                        <Col>
                            <b className="d-inline-block me-1">From:</b>
                            {message.sender.name}
                        </Col>
                        <Col>
                            <b className="d-inline-block me-1">Subject:</b>
                            {message.title}
                        </Col>
                        <Col>
                            <b className="d-inline-block me-1">Received:</b>
                            {dayjs(message.createdAt).format('DD-MMMM-YYYY HH:ss')}
                        </Col>
                    </Row>
                </Container>
            </Accordion.Header>
            <Accordion.Body>{message.body}</Accordion.Body>
        </Accordion.Item>
    );
};

export default InboxItem;
