import React, { useState } from 'react';
import Welcome from '../welcome/Welcome';
import NewMessage from '../newMessage/NewMessage';
import MessageApi from '../../services/messageApi';
import { MessageApiSuccessResponse } from '../../types/MessageApiResponse';
import { MessageApiEventType } from '../../types/MessageApiEventType';
import { User } from '../../types/User';
import Inbox from '../inbox/Inbox';
import { useWebSocket } from '../hooks/useWebSocket';

const App = () => {
    const [user, setUser] = useState<{ id: number; name: string } | null>(null);

    const handleUserLogin = (response: MessageApiSuccessResponse<unknown>) => {
        if (response.type === MessageApiEventType.Login) {
            const { id, name } = response.data as User;
            setUser({ id, name });
        }
    };

    useWebSocket(handleUserLogin);

    const handleNameChanged = (newName: string) => {
        const payload = {
            type: 'messageApi/login',
            payload: {
                name: newName,
            },
        };
        MessageApi.getInstance().sendMessage(JSON.stringify(payload));
    };

    return (
        <div className="container pt-5">
            {user === null ? (
                <Welcome onNameChanged={handleNameChanged} />
            ) : (
                <>
                    <NewMessage name={user.name} />
                    <Inbox userId={user.id} />
                </>
            )}
        </div>
    );
};

export default App;
